import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { filterCarsByMake, filterCarsByRentalPrice, filterCarsByMileage } from 'services/api';

//   thunk    
export const requestFilteredCars = createAsyncThunk(
  'cars/filter',
  async (filters, thunkAPI) => {
    try {
      const { make, price, mileage } = filters; //   : ,   
      let filteredCars = []; //      

      //   
      if (make) {
        const carsFilteredByMake = await filterCarsByMake(make); //     
        filteredCars = carsFilteredByMake; //   
      }

      //    
      if (price) {
        const carsFilteredByPrice = await filterCarsByRentalPrice(price); //      
        //     ,       ,    
        filteredCars = filteredCars.length ? filteredCars.filter(car => carsFilteredByPrice.includes(car)) : carsFilteredByPrice;
      }

      //   
      if (mileage) {
        const carsFilteredByMileage = await filterCarsByMileage(mileage); //     
        //     ,       ,    
        filteredCars = filteredCars.length ? filteredCars.filter(car => carsFilteredByMileage.includes(car)) : carsFilteredByMileage;
      }

      return filteredCars; //    
    } catch (error) {
      return thunkAPI.rejectWithValue(error.message); //   ,  thunk  
    }
  }
);

//   
const INITIAL_STATE = {
  filteredCars: [], //   
  isLoading: false, // ,  ,   
  error: null, // ,       
};

//  slice   
const carsSlice = createSlice({
  name: 'cars',
  initialState: INITIAL_STATE,
  extraReducers: (builder) => {
    builder
      //   "pending"    
      .addCase(requestFilteredCars.pending, (state) => {
        state.isLoading = true; // ,   
        state.error = null; //   ,   
      })
      //   "fulfilled"    
      .addCase(requestFilteredCars.fulfilled, (state, action) => {
        state.isLoading = false; // ,   
        state.filteredCars = action.payload; //    
      })
      //   "rejected"    
      .addCase(requestFilteredCars.rejected, (state, action) => {
        state.isLoading = false; // ,   
        state.error = action.payload; //  
      });
  },
});

//   slice
export const carsReducer  = carsSlice.reducer;